    #t_1_6
    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.6.	Documentación de soporte: órdenes de servicio, planes de acción, reportes y bitácoras
      
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p La documentación de soporte en la operación turística es un conjunto de registros administrativos y técnicos que garantizan la trazabilidad, la seguridad y la calidad del servicio. Estos documentos permiten planificar, ejecutar y evaluar las actividades turísticas de manera organizada, facilitando la toma de decisiones y la mejora continua. Según el Ministerio de Comercio, Industria y Turismo (MINCIT, 2020), la documentación operativa es un componente esencial en los procesos de gestión turística, pues “proporciona evidencia verificable de la correcta ejecución de las actividades y del cumplimiento de las normas de seguridad y sostenibilidad”.
      p Los principales documentos de soporte utilizados en la operación de servicios turísticos son:

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      h5 Órdenes de servicio
      p Las órdenes de servicio son los documentos que formalizan la ejecución de una actividad o recorrido turístico. En ellas se detallan los servicios contratados, los responsables de la operación, los horarios, las rutas y los recursos logísticos asignados.

      p Estas órdenes garantizan que cada servicio se realice conforme a los lineamientos establecidos y permiten verificar el cumplimiento de los compromisos adquiridos con el cliente. Además, constituyen un instrumento de control y seguimiento dentro del proceso de gestión operativa.

      h5 Planes de acción
      p Los planes de acción especifican cómo se desarrollará una actividad turística, incluyendo los objetivos, las tareas, los responsables y los tiempos de ejecución.

      p Estos planes permiten coordinar los equipos humanos y materiales, así como anticipar posibles contingencias. En el contexto del turismo sostenible, integran estrategias de minimización de impactos ambientales y culturales, fortaleciendo la responsabilidad social de las operaciones.

      p Como señala la Organización Mundial del Turismo (OMT, 2019), “el plan de acción traduce la planeación estratégica en procedimientos concretos que orientan el trabajo diario”.

      h5 Reportes
      p Los reportes turísticos son documentos estructurados que recogen información objetiva sobre la ejecución de una actividad, sus incidencias, resultados y recomendaciones.

      p Su propósito es evaluar el servicio, generar información útil para la toma de decisiones, y fortalecer la retroalimentación del personal operativo y la mejora de procesos.

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      .p “Un reporte bien elaborado no solo informa, sino que orienta la mejora y respalda las decisiones de gestión.”
      p — Organización Mundial del Turismo (OMT, 2021)

    .row.justify-content-center
      .col-lg-11
