                p.color-adicional-4 Las bitácoras turísticas son registros cronológicos y narrativos que reflejan el día a día de la operación turística. Se elaboran por el guía, conductor o coordinador de grupo, y permiten dejar evidencia del desarrollo real del recorrido, más allá de los aspectos formales del reporte
                
              .col-lg-2.col-6
